/* @import "https://fonts.googleapis.com/css?fanily=Roboto:100,200,300,400,500,600,700,800,900"; */
body,
html {
  width: 100vw;
  height: 100vh;
  /* font-family: "Roboto", sans-serif; */
  /* background: red; */
  /* color: #fff; */
  overflow-x: hidden;
}

/**colors for text and background**/
.hard-brand-background {
  background: #0f4700;
}
.light-brand-background {
  background: #7db833;
}

.pale-brand-background {
  background: #95cc7b;
}
/** for text */
.hard-brand-color {
  color: #0f4700;
}
.light-brand-color {
  color: #7db833;
}

.pale-brand-color {
  color: #95cc7b;
}

/**icons font weight */
.normal-icon-size {
  font-size: 2rem;
}

.bigger-icon-size {
  font-size: 4rem;
}
.extra-bigger-icon-size {
  font-size: 6rem;
}
.absolute-icon-center-left {
  position: absolute;
  top: -50%;
  left: -5%;
  /* bottom: 50%; */
}
.absolute-icon-center-right {
  position: absolute;
  top: 50%;
  right: -5%;
  bottom: 50%;
  z-index: 1;
}
/** Removing underline on the link from react-router-dom**/
a:-webkit-any-link {
  text-decoration: none;
  color: inherit;
}

/**about under lines*/
.border-under {
  height: 2px;
  width: 100%;
}

/** font sizes */
.font-size-normal {
  font-size: 1rem;
}

/**Navigation animation for the text link colors **/
.navigation-color {
  color: #7db833 !important;
}

.navigation-link {
  position: relative;
}
.navigation-link::after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  height: 0px;
  background: #7db833;
  bottom: -6px;
  transition: height 0.35s ease-in;
}

.nav-item:hover .navigation-link::after {
  height: 5px;
}

/**THE SLIDE SHOW FOR THE HOME PAGE*/
.slideshow-wrapper {
  overflow: hidden;
}
.slideshow {
  display: flex;
  width: 600%;
  height: 95%;
  position: relative;
  animation: slideshowAnimation 24s infinite;
}

/**animating a slide show right now really qyick*/
@keyframes slideshowAnimation {
  0% {
    left: 0;
  }
  10% {
    left: 0;
  }
  15% {
    left: -100%;
  }
  25% {
    left: -100%;
  }
  35% {
    left: -200%;
  }
  45% {
    left: -200%;
  }
  55% {
    left: -300%;
  }
  65% {
    left: -300%;
  }
  75% {
    left: -400%;
  }
  85% {
    left: -400%;
  }
  95% {
    left: -500%;
  }
  100% {
    left: -500%;
  }
}
.slide {
  width: 100%;
  height: 100vh;
}
.slide:nth-child(1) {
