.content {
  width: 500px;
  position: relative;
  top: 15%;
  left: 5%;
}

.content h3 {
  font-family: sans-serif;
  font-size: 80px;
  font-weight: 400;
  color: #fff;
  margin: 0;
  padding: 0;
}

.content p {
  font-size: 20px;
  color: #ccc;
}

.content button {
  width: 140px;
  padding: 10px;
  margin-top: 10px;
  border: none;
  outline: none;
  font-size: 15px;
  letter-spacing: 1px;
  color: #fff;
  background: transparent;
  border-bottom: 1px solid #fff;
}

.content i {
  margin-left: 8px;
}

/**End of the slider*/
.simple-title::after {
  content: "";
  position: absolute;
  height: 2px;
  left: 0;
  right: 0;
  bottom: 0;
  background: #7db833;
}
.simple-titled-content::before {
  content: "";
  position: absolute;
  /* height: 1px; */
  left: 0;
  right: 97%;
  bottom: 0;
  top: 0;
  background: #7db833;
  z-index: 1;
}
.simple-titled-content {
  background: linear-gradient(90deg, #9ab57a, #fff);
}

.slighted-photo-container {
  position: relative;
  background-color: #ffff;
  -webkit-box-shadow: 10px 10px 5px 0px rgba(178, 184, 178, 1);
  -moz-box-shadow: 10px 10px 5px 0px rgba(178, 184, 178, 1);
  box-shadow: 10px 10px 5px 0px rgba(178, 184, 178, 1);
}

.rotate-left {
  transform: rotateZ(-5deg);
}
.rotate-right {
  transform: rotateZ(5deg);
}

.light-lined-content::before {
  content: "";
  position: absolute;
  /* height: 1px; */
  left: 0;
  right: 97%;
  bottom: 0;
  top: 0;
  background: #7db833;
  z-index: 1;
}
.light-lined-content-1::before {
